.productCardContainer{display:flex;flex-wrap:wrap;justify-content:center}.productCardContainer .productCard{margin:1rem;opacity:.6;width:250px;height:350px;padding:.5rem;background-position:center;background-size:cover;background-repeat:no-repeat;position:relative;display:flex;align-items:center;justify-content:center;    gap: 2rem;overflow:hidden}.productCardContainer .productCard:hover .title{transform:translateY(0)}.productCardContainer .productCard .price{color:#fff;position:absolute;left:50%;top:50%;transform:translate(-50%, -50%);z-index:3;background-color:rgba(15,15,15,.432);padding:10px;border-radius:20px;text-align:center;font-weight:700}.productCardContainer .productCard .title{color:#fff;background-color:rgba(0,0,0,.61);position:absolute;bottom:0;left:0;overflow:auto;text-align:left;width:100%;transform:translateX(100%);transition:all .7s ease-in-out;z-index:3}.App{display:flex;flex-wrap:wrap;justify-content:center;background-color:#9cd8d8}